import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    aboutInfo: [],
    projectsInfo: []
  },
  mutations: {
    SET_ABOUT_INFO(state, aboutInfo) {
      state.aboutInfo = aboutInfo;
    },
    SET_PROJECTS_INFO(state, projectsInfo) {
      state.projectsInfo = projectsInfo;
    }
  },
  actions: {
    fetchAboutData() {
      // The place where you do the async calls to backend
      return new Promise(resolve => {
        setTimeout(async () => {
          const res_about = await fetch("data/about.json"); // Assume nothing goes wrong since local
          const val_about = await res_about.json();
          resolve(val_about);
        });
      });
    },
    async fetchAbout({dispatch, commit}) {
      const aboutJson = await dispatch("fetchAboutData");
      commit("SET_ABOUT_INFO", aboutJson);
    },
    fetchProjectsData() {
      // The place where you do the async calls to backend
      return new Promise(resolve => {
        setTimeout(async () => {
          const res_projects = await fetch("data/projects.json");
          const val_projects = await res_projects.json();
          resolve(val_projects);
        });
      });
    },
    async fetchProjects({dispatch, commit}) {
      const projectsJson = await dispatch("fetchProjectsData");
      commit("SET_PROJECTS_INFO", projectsJson);
    },
    updateProjects({commit}, myJson) {
      commit("SET_PROJECTS_INFO", myJson);
    },
    async search({dispatch}, {text}) {  // TODO: Update the search to cover {text, keywords} as well
      const myJson = await dispatch("fetchProjectsData");  // To get the most recent from server
      const values = myJson.filter(val => val.name.toLowerCase().includes(text.toLowerCase()));
      dispatch("updateProjects", values);
    }
  },
  getters: {
    projectsInfo(state) {
      return state.projectsInfo;
    },
    aboutInfo(state) {
      return state.aboutInfo;
    }
  },
  modules: {}
});
