<template>
  <div>
    <b-img
        v-if="width >= 768"
        :src="image_path"
        img-alt="Project-info"
        img-top
        style="
              position:absolute;
              left:50%;
              transform: translateX(-50%);
              height: 100%;"
    />
    <b-img
        v-else
        :src="image_path"
        img-alt="Project-info"
        img-top
        style="
              position:relative;
              top:40%;
              transform: translateY(-50%);
              width: 100%;"
    />
  </div>
</template>

<script>
  export default {
    name: "ImageComponent",
    props: ["image_path", "width"]
  };
</script>
