<template>
  <div>
    <b-container>
      <CarouselCard :interval="0" height="600px" arrow="hover">
        <CarouselCardItem v-for="info in aboutInfo" :key="info.title">
          <card
              :image_path="info.image"
              :title="info.title"
              :description="info.text"
          ></card>
        </CarouselCardItem>
      </CarouselCard>
    </b-container>
  </div>
</template>

<script>
  import CardAbout from "@/components/CardAbout.vue";
  import { CarouselCard, CarouselCardItem } from 'vue-carousel-card'
  import 'vue-carousel-card/styles/index.css'
  import {mapGetters} from "vuex";

  export default {
    components: {
      CarouselCard,
      CarouselCardItem,
      card: CardAbout
    },
    computed: {
      ...mapGetters(["aboutInfo"])
    },
    mounted() {
      this.fetchData();
    },
    methods: {
      async fetchData() {
        await this.$store.dispatch("fetchAbout");
      }
    }
  }
</script>

