<template>
  <div class="card">
    <!-- Desktop -->
    <b-row class="outer_row" v-if="windowWidth >= 768">
      <b-col class="image" cols="3">
        <imageComponent :width="windowWidth" :image_path="image_path"/>
      </b-col>
      <b-col>
        <!--Takes the rest of the columns-->
        <b-row class="title">
          {{title}}
        </b-row>
        <b-row class="description" fluid>{{description}}</b-row>
      </b-col>
    </b-row>
    <!-- Mobile -->
    <b-row class="outer_row" v-else>
      <b-col>
        <b-row class="image-mobile">
          <imageComponent :width="windowWidth" :image_path="image_path"/>
        </b-row>
        <b-row class="title-mobile">
          <h1>{{title}}</h1>
        </b-row>
        <b-row class="description-mobile" fluid>{{description}}</b-row>
      </b-col>
    </b-row>
  </div>
</template>

<script>
  import ImageComponent from "@/components/ImageComponent.vue";

  export default {
    name: "CardAbout",
    components: {
      imageComponent: ImageComponent
    },
    props: ["image_path", "title", "description"],
    data() {
      return {
        windowWidth: window.innerWidth
      };
    },
    mounted() {
      window.addEventListener("resize", () => {
        this.windowWidth = window.innerWidth;
      });
    }
  };
</script>

<style lang="scss">
  // General
  .card {
    height: 100%;
    width: 100%;
    padding: 10px;
    background: #263238;
    border-radius: 5px;
  }

  .carousel-card-indicator.is-active button {
    background-color: #000a12;
  }

  .outer_row {
    height: 100%;
    width: 100%;
    margin: 0;
    background: #cfd8dc;
    overflow: hidden;
  }

  // Image related
  .image {
    overflow: hidden;
  }

  .image-mobile {
    @extend .image;
    height: 200px;
  }

  // Title related
  .title {
    font-weight: bold;
    padding: 16px;
    font-size: 3em;
  }

  .title-mobile {
    @extend .title;
    padding-bottom: 8px;
    font-size: 1.5em;
  }

  // Description related
  .description {
    padding-left: 16px;
    padding-right: 16px;
    font-size: 1.2em;
  }

  .description-mobile {
    @extend .description;
    font-size: 0.9em;
  }
</style>
